import { addDays, format } from "date-fns";
import React, { useState } from "react";
import { directusLoaderLink } from "~/utils/constants";
import ConfirmationModal from "./Modal/confirmationModal";
import { toast } from "react-toastify";

function ActivityContent({
  data,
  date,
  locationId,
  refresh,
  setRefresh,
  head,
  isAdmin,
}) {
  const [title, setTitle] = useState(
    !!data?.activity_name ? data?.activity_name : ""
  );
  const DateFormat = 'yyyy-MM-dd'
  const [description, setDescription] = useState(
    !!data?.activity_description ? data?.activity_description : ""
  );
  const [time, SetTime] = useState(!!data?.time ? data?.time : "");
  const [modalIsOpen, setIsOpen] = useState(false);
  const [modalLoader, setModalLoader] = useState(false);
  const saveHandle = async () => {
    if (!!data?.id && data?.id) {
      // update scenario
      if (
        title?.trim() !== data?.activity_name ||
        time !== data?.time ||
        description?.trim() !== data?.activity_description
      ) {
        await directusLoaderLink
          .items("activities")
          .updateOne(data?.id, {
            activity_name: title?.trim(),
            activity_description: description?.trim(),
            time: time,
          })
          .then((res) => {
            // toast.success("Updated successfully");
            setRefresh(!refresh);
          })
          .catch((err) => {
            // navigate(".", { replace: true });
            console.log(err);
            // toast.error(err);
          });
      }
    } else {
      // create scenario
      if (title?.trim() && time !== "") {
        await directusLoaderLink
          .items("activities")
          .createOne({
            date: format(date,DateFormat),
            activity_name: title?.trim(),
            activity_description: description?.trim(),
            time: time,
            location_id: locationId,
          })
          .then((res) => {
            // toast.success(" Created successfully");
            setRefresh(!refresh);
          })
          .catch((err) => {
            // navigate(".", { replace: true });
            console.log(err);
            // toast.error(err);
          });
      }
    }

    localStorage.setItem(
      "ActivityWacthedWeek",
      new Date(date)?.getDay() == 0
        ? format(addDays(new Date(date), -2), "yyyy-MM-dd")
        : date
    );
  };
  const deleteHandle = async () => {
    if (data?.id) {
      setModalLoader(true);
      await directusLoaderLink
        .items("activities")
        .deleteOne(data?.id)
        .then((res) => {
          toast.success("Data deleted successfully");
        })
        .catch((err) => {
          console.log(err);
        });

      location.reload();
      setRefresh(!refresh);
      setModalLoader(false);
    }
  };
  const cancelHandle = () => {
    setIsOpen(false);
  };
  React.useEffect(() => {
    if (title?.length > 100) {
      setTitle(title?.substring(0, 100));
    }
    if (description?.length > 100) {
      setDescription(description?.substring(0, 100));
    }
  }, [title, description]);
  return (
    <div className="flex flex-col">
      <div className="flex flex-col" onBlur={saveHandle}>
        <div className="flex border-b border-[#E1E1E1] relative z-[1]">
          <div className="min-w-[135px] px-2 py-[30px] relative z-[1] block text-center">
            {head && <span className="inline-block uppercase p-2">time</span>}
            <input
              type="time"
              id="activity-time"
              name="activity"
              value={time?.slice(0, 5) || "00:00"}
              onChange={(e) => {
                SetTime(e.target.value);
              }}
              className=" rounded-md  text-primary shadow-sm border border-primary focus:border-primary placeholder:font-bold placeholder:text-[#6E7878] placeholder:text-[14px] focus:outline-none focus:ring-1 focus:ring-primary sm:text-sm"
            />
          </div>
          <div className="px-4 py-[30px] w-full">
            {head && (
              <span className="inline-block uppercase py-2 px-4">Activity</span>
            )}

            <div className="relative activity-title">
              <textarea
                type="text"
                id="activity-title"
                name="activity"
                placeholder="Enter the activity title"
                value={title || ""}
                onChange={(e) => {
                  setTitle(e.target.value);
                }}
                className="resize-none  min-h-[80px] w-full px-4 py-3 flex rounded-md justify-center items-center text-primary shadow-sm border border-primary focus:border-primary placeholder:font-bold placeholder:text-[#6E7878] placeholder:text-[14px] focus:outline-none focus:ring-1 focus:ring-primary sm:text-sm"
              />
              <span className="text-[10px] absolute bottom-0 right-2">
                {title?.length}/100
              </span>
            </div>
            <div className="relative activity-description">
              <textarea
                type="text"
                name="description"
                id="description"
                placeholder="Enter the activity description"
                value={description || ""}
                onChange={(e) => setDescription(e.target.value)}
                className="resize-none min-h-[80px] mt-4 block w-full rounded-md border border-primary py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium focus:border-primary  focus:outline-none focus:ring-primary sm:text-sm"
              />
              <span className="text-[10px] absolute bottom-0 right-2">
                {description?.length}/100
              </span>
            </div>
            {isAdmin && (
              <div
                className={`flex justify-end cursor-pointer absolute bottom-[5px] right-[15px] ${
                  !!data?.id && data?.id && isAdmin ? "" : "hidden"
                }`}
                onClick={() => {
                  return setIsOpen(true);
                }}
              >
                <i className="ri-delete-bin-line"></i>
              </div>
            )}
          </div>
        </div>
      </div>

      {modalIsOpen && (
        <ConfirmationModal
          modalIsOpen={modalIsOpen}
          setIsOpen={setIsOpen}
          content="Are you sure to delete data?"
          callFunction={deleteHandle}
          cancelHandle={cancelHandle}
          Loader={modalLoader}
        />
      )}
    </div>
  );
}

export default ActivityContent;
